import React, {Component} from 'react';
import {skillsIcon, skillsIcon2} from '../CommonFunctions'
import {Button} from "@material-ui/core";
import {TECHNOLOGIES_STATEMENT} from '../../utils/constants.js';

export default class Technologies extends Component {

    constructor(props) {
        super(props);
        this.state = {
            extraTechnologiesOpen: false
        };
    }

    render() {
        return(
            <li className="wow fadeInLeft" data-wow-duration="0.8s">
                <div className="inner">
                    <div className="skillheading">
                        <h1><span>Technologies</span></h1>
                    </div>
                    <div className="title">
                    </div>
                    <div className="text" style={{textAlign: "center"}}>
                        <div style={{marginBottom: "5%", fontSize: "large"}}>{TECHNOLOGIES_STATEMENT}</div>
                        {skillsIcon("languages fab fa-linux fa-4x fa-fw", "Linux")}
                        {skillsIcon("languages fab fa-apple fa-4x fa-fw", "MacOS")}
                        {skillsIcon("languages fab fa-windows fa-4x fa-fw", "Windows")}
                        {this.state.extraTechnologiesOpen && this.renderExtraTechnologies()}
                        <Button style={{height: "75px", width: "75px", display: "inline-flex", alignItems: "flex-end"}} onClick={() => this.setState(prevState => ({extraTechnologiesOpen: !prevState.extraTechnologiesOpen}))}>
                                {!this.state.extraTechnologiesOpen ? <i className="arrow_bounce fas fa-angle-double-down fa-2x"></i> : <i className="arrow_bounce fas fa-angle-double-up fa-2x"></i>}
                        </Button>
                    </div>
                </div>
            </li>
        )
    }

    renderExtraTechnologies() {
        return (
            <div>
                {skillsIcon("languages fab fa-react fa-4x fa-fw", "React")}
                {skillsIcon("languages fab fa-bootstrap fa-4x fa-fw", "Bootstrap")}
                {skillsIcon("languages fab fa-node-js fa-4x fa-fw", "Node.js")}
                {skillsIcon2("file-icons:jest", "Jest")}
                {skillsIcon("languages fas fa-feather fa-4x fa-fw", "Maven")}
                {skillsIcon2("mdi:numeric-5-circle", "JUnit")}
                {skillsIcon2("file-icons:webpack", "Webpack")}
                {skillsIcon("languages fab fa-docker fa-4x fa-fw", "Docker")}
                {skillsIcon2("file-icons:leaflet", "Leaflet")}
                {skillsIcon2("cib:json", "JSON")}
                {skillsIcon2("cib:svg", "SVG")}
                {skillsIcon2("simple-icons:googleearth", "Google KML")}
                {skillsIcon2("simple-icons:microsoftsqlserver", "Microsoft SQL Server")}
                {skillsIcon2("whh:mysqltwo", "MySQL")}
                {skillsIcon2("ph:stack-bold", "Google SQL")}
                {skillsIcon2("whh:pgsql", "pgsql")}
                {skillsIcon2("simple-icons:googlemaps", "Google Maps API")}
                {skillsIcon2("dashicons:rest-api", "Restful API")}
                {skillsIcon("languages fas fa-registered fa-4x fa-fw", "R Studio")}
                {skillsIcon("languages fas fa-terminal fa-4x fa-fw", "Tmux")}
                {skillsIcon2("logos:spark", "Spark")}  
            </div>
        )
    }
}