import React, {Component} from 'react';
import {skillsIcon, skillsIcon2} from '../CommonFunctions'
import {Button} from "@material-ui/core";
import {TOOLS_STATEMENT} from '../../utils/constants.js';

export default class Tools extends Component {

    constructor(props) {
        super(props);
        this.state = {
            extraToolsOpen: false
        };
    }

    render() {
        return(
            <li className="wow fadeInLeft" data-wow-duration="0.8s">
                <div className="inner">
                    <div className="skillheading">
                        <h1><span>Tools</span></h1>
                    </div>
                    <div className="title">
                    </div>
                    <div className="text" style={{textAlign: "center"}}>
                        <div style={{marginBottom: "5%", fontSize: "large"}}>{TOOLS_STATEMENT}</div>
                        {skillsIcon("languages fas fa-code-branch fa-4x fa-fw", "Git")}
                        {skillsIcon("languages fab fa-github fa-4x fa-fw", "GitHub")}
                        {skillsIcon("languages fab fa-slack fa-4x fa-fw", "Slack")}
                        {this.state.extraToolsOpen && this.renderExtraTools()}
                        <Button style={{height: "75px", width: "75px", display: "inline-flex", alignItems: "flex-end"}} onClick={() => this.setState(prevState => ({extraToolsOpen: !prevState.extraToolsOpen}))}>
                                {!this.state.extraToolsOpen ? <i className="arrow_bounce fas fa-angle-double-down fa-2x"></i> : <i className="arrow_bounce fas fa-angle-double-up fa-2x"></i>}
                        </Button>
                    </div>
                </div>
            </li>
        )
    }

    renderExtraTools() {
        return (
            <div>
                {skillsIcon("languages fab fa-npm fa-4x fa-fw", "NPM")}
                {skillsIcon2("cib:travisci", "Travis CI")}
                {skillsIcon2("cib:code-climate", "Code Climate")}
                {skillsIcon2("bx:bxl-microsoft-teams", "Microsoft Teams")}
                {skillsIcon2("logos:zenhub-icon", "ZenHub")}
                {skillsIcon2("cib:eclipseide", "Eclipse")}
                {skillsIcon2("akar-icons:vscode-fill", "VSCode")}
                {skillsIcon2("logos:intellij-idea", "IntelliJ Idea")}
                {skillsIcon2("logos:clion", "CLion")}
                {skillsIcon2("logos:pycharm", "PyCharm")}
            </div>
        )
    }
}